.main-div, .webcam-div {
    width: 45vh;
    min-width: 300px;
    padding-top: 80vh;
}

.main-div {
    position: relative;
}

.main-div * {
    position: absolute;
}

.webcam-div {
    top: 0;
    left: 0;
    overflow: hidden;
}

.webcam-video {
    top: 0;
    left: 0;
}

.face-box {
    position: absolute;
    top: 0;
    left: 0;
    border: solid blue;
    z-index: 3;
    pointer-events: none;
    background: transparent;
}

.check-photo-img {
    top: 0;
    left: 0;
}

.typography {
    position: absolute;
    left: 5%;
    top: 2%;
    z-index: 1;
    font-weight: bold;
    font-size: 40px;
    color: white;
    text-shadow: 0px 0px 5px black;
}

button {
    position: absolute;
    bottom: 5%;
    z-index: 1;
    font-size: 20px;
    border-radius: 50px;
    cursor: pointer;

    background: no-repeat center;
    background-size: 70%;
    height: 50px;
    border: none;
}

.fileInput {
    display: none;
    position: absolute;
}

.main-button {
    left: 50%;
    -webkit-transform: translate(-50%);
            transform: translate(-50%);
    padding-right: 20px;
    padding-left: 20px;
    background-color: white;
}

.secondary-button {
    background-color: deeppink;
    width: 50px;
    left: 5%;
}

.next-button {
    background-color: white;
    width: 50px;
    right: 5%;
}

.back-button {
    background-image: url(data:image/png;base64,);
}

.photo-button {
    background-image: url(data:image/png;base64,);
}

.next-button {
    background-image: url(data:image/png;base64,);
}

.oval-container {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    overflow: hidden;
}

.oval {
    position: absolute;
    top: 40%;
    left: 50%;
    height: 60%;
    width: 70%;
    box-shadow: 0 0 9999px 99999px rgba(0, 0, 0, 0.7);
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    border: 2px solid rgba(255, 255, 255, 0.5);
    border-radius: 40%;
}

.vl {
    position: absolute;
    height: 70%;
    width: 2px;
    left: 50%;
    top: 5%;
    background-color: rgba(255, 255, 255, 0.5);
}

.hl {
    position: absolute;
    width: 90%;
    height: 2px;
    left: 5%;
    top: 37%;
    background-color: rgba(255, 255, 255, 0.5);
}
.checkbox {
    margin: 20px;
    z-index: 4;
    display: block;
    position: relative;
    padding-left: 35px;
    cursor: pointer;
    font-size: 22px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

/* Hide the browser's default checkbox */
.checkbox input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
}

/* Create a custom checkbox */
.checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 25px;
    width: 25px;
    background-color: #eee;
}

/* On mouse-over, add a grey background color */
.checkbox:hover input ~ .checkmark {
    background-color: #ccc;
}

/* When the checkbox is checked, add a blue background */
.checkbox input:checked ~ .checkmark {
    background-color: #2196F3;
}

/* Create the checkmark/indicator (hidden when not checked) */
.checkmark:after {
    content: "";
    position: absolute;
    display: none;
}

/* Show the checkmark when checked */
.checkbox input:checked ~ .checkmark:after {
    display: block;
}

/* Style the checkmark/indicator */
.checkbox .checkmark:after {
    left: 9px;
    top: 5px;
    width: 5px;
    height: 10px;
    border: solid white;
    border-width: 0 3px 3px 0;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
}
